import { Image } from '@chakra-ui/react'
import styled from '@emotion/styled'

const InfoCard = styled.button`
  background-color: ${({ theme }) => theme.backgroundInteractive};
  padding: 1rem;
  outline: none;
  border: 1px solid;
  border-radius: 12px;
  width: 100% !important;
  &:focus {
    background-color: ${({ theme }) => theme.hoverState};
  }
  border-color: ${({ theme, isActive }) => (isActive ? theme.accentActive : 'transparent')};
`

const OptionCard = styled(InfoCard)`
  display: flex;
  flex-direction: row;
  align-items: center;
  justify-content: space-between;
  margin-top: 2rem;
  padding: 1rem;
`

const OptionCardClickable = styled(OptionCard)`
  margin-top: 0;
  border: ${({ active, theme }) => active && `1px solid ${theme.accentActive}`};
  &:hover {
    cursor: ${({ clickable }) => clickable && 'pointer'};
    background-color: ${({ theme }) => theme.hoverState};
  }
  opacity: ${({ disabled }) => (disabled ? '0.5' : '1')};
`

const OptionCardLeft = styled.div`
  display: flex;
  flex-flow: row nowrap;
  justify-content: center;
  height: 100%;
`

const HeaderText = styled.div`
  display: flex;
  flex-flow: row nowrap;
  align-items: center;
  justify-content: center;
  color: ${(props) => (props.color === 'blue' ? ({ theme }) => theme.accentAction : ({ theme }) => theme.textPrimary)};
  font-size: 16px;
  font-weight: 600;
`

const IconWrapper = styled.div`
  display: flex;
  flex-flow: row nowrap;
  align-items: center;
  justify-content: center;
  padding-right: 12px;
  & > img,
  span {
    height: ${({ size }) => (size ? size + 'px' : '28px')};
    width: ${({ size }) => (size ? size + 'px' : '28px')};
  }
`

export default function Option({
  link = null,
  clickable = true,
  size,
  onClick = null,
  color,
  header,
  subheader,
  icon,
  isActive = false,
  id,
}) {
  // const content = (
  //   <Card paddingX={20} paddingY={10} mb={10} onClick={onClick}>
  //     <Flex justifyContent="space-between" alignItems="center">
  //       <Box width={30} height={30}>
  //         <Image src={icon} alt="Icon" width={30} height={30} />
  //         {/*<Icon height={30} width={30} />*/}
  //       </Box>
  //       <Text fontSize={14} fontWeight={600}>
  //         {header}
  //       </Text>
  //     </Flex>
  //   </Card>
  // )

  const content = (
    <OptionCardClickable
      id={id}
      onClick={onClick}
      clickable={clickable && !isActive}
      active={isActive}
      data-testid="wallet-modal-option"
    >
      <OptionCardLeft>
        <HeaderText color={color}>
          <IconWrapper size={size}>
            <Image src={icon} alt="Icon" />
          </IconWrapper>
          {header}
        </HeaderText>
        {/*{subheader && <SubHeader>{subheader}</SubHeader>}*/}
      </OptionCardLeft>
      {/*{isActive && <CheckIcon />}*/}
    </OptionCardClickable>
  )
  if (link) {
    return (
      <a target="_blank" href={link} rel="noreferrer">
        {content}
      </a>
    )
  }

  return content
}
