import React from 'react'
import { Button, Text, Box } from '@chakra-ui/react'
import styled from '@emotion/styled'

export const StyledButton = styled(Button)`
  &:hover {
    background-color: ${({ theme }) => theme.colors.foreground} !important;
    color: ${({ theme }) => theme.colors.background};
  }
`

const ActionButton = ({ validators, ...restProps }) => {
  const failedValidator = validators.find((e) => !e.should)

  const mergedProps = {
    ...restProps,
    ...failedValidator?.fallbackProps,
  }
  const { children, onClick, disabled, helperText, isLoading = false } = mergedProps

  return (
    <Box style={{ textAlign: 'center' }} mt={'15px'}>
      <StyledButton
        scale="sm"
        variant="solid"
        color="gray.100"
        background="foreground"
        onClick={onClick}
        isDisabled={disabled}
        isLoading={isLoading}
        loadingText={children}
      >
        {children}
      </StyledButton>
      {helperText && (
        <Text color="foreground" fontSize="12px" textAlign="center" mb={'10px'} lineHeight={1} mt={2}>
          {helperText}
        </Text>
      )}
    </Box>
  )
}

export default ActionButton
