import { Box } from '@chakra-ui/react'
import React, { useCallback, useEffect, useState } from 'react'
import { useDispatch } from 'react-redux'
import { AppConfig, showConnect } from '@stacks/connect'

import PendingView from '../../components/WalletModal/PendingView'
import WalletOptions from '../../components/WalletModal/WalletOptions'
import { getConnection } from '../../connection/utils'
import { updateSelectedWallet } from '../../state/user/reducer'
import SolanaWalletOptions from './SolanaWalletOptions'
import { useWallet } from '@solana/wallet-adapter-react'
import useStacks from './hooks/useStacks'

const WALLET_VIEWS = {
  OPTIONS: 'options',
  ACCOUNT: 'account',
  PENDING: 'pending',
}

const AllWallets = () => {
  const [walletView, setWalletView] = useState(WALLET_VIEWS.OPTIONS)

  const dispatch = useDispatch()

  const walletAdapter = useWallet()

  const { userSession } = useStacks()

  useEffect(() => {
    if (walletAdapter.wallet && !walletAdapter.connected) {
      walletAdapter
        .connect()
        .then(() => {
          // dispatch(updateSolanaSelectedWallet({ wallet: walletName }))
        })
        .catch((e) => {
          console.log(e)
          setWalletView(WALLET_VIEWS.OPTIONS)
        })
    }
  }, [walletAdapter, walletAdapter.wallet])

  const tryActivation = useCallback(async (connector) => {
    try {
      setWalletView(WALLET_VIEWS.PENDING)

      if (connector === 'Phantom') {
        if (!walletAdapter.connected) {
          walletAdapter.select(connector)
        }

        dispatch(updateSelectedWallet({ wallet: connector }))
      } else if (connector === 'stacks') {
        showConnect({
          appDetails: {
            name: 'Bandit',
            icon: window.location.origin + '/logo512.png',
          },
          redirectTo: '/',
          onFinish: () => {
            dispatch(updateSelectedWallet({ wallet: connector }))
          },
          userSession,
        })
      } else {
        const connectionType = getConnection(connector).type
        await connector.activate()
        dispatch(updateSelectedWallet({ wallet: connectionType }))
      }
    } catch (error) {
      setWalletView(WALLET_VIEWS.OPTIONS)
      console.debug(`web3-react connection error: ${error}`)
    }
  }, [])

  const getModalContent = () => {
    if (walletView === WALLET_VIEWS.OPTIONS) {
      return (
        <>
          <WalletOptions tryActivation={tryActivation} />
          <SolanaWalletOptions tryActivation={tryActivation} showTitle={false} />
        </>
      )
    }

    if (walletView === WALLET_VIEWS.PENDING) {
      return <PendingView goBack={() => setWalletView(WALLET_VIEWS.OPTIONS)} />
    }
  }
  return <Box>{getModalContent()}</Box>
}

export default AllWallets
