import { PublicKey } from '@solana/web3.js'
import {
  Liquidity,
  LIQUIDITY_PROGRAM_ID_V4,
  Market,
  Percent,
  SPL_ACCOUNT_LAYOUT,
  Token,
  TOKEN_PROGRAM_ID,
  TokenAmount,
} from '@raydium-io/raydium-sdk'

export async function getTokenAccountsByOwner(connection, owner) {
  const tokenResp = await connection.getTokenAccountsByOwner(owner, {
    programId: TOKEN_PROGRAM_ID,
  })

  const accounts = []

  for (const { pubkey, account } of tokenResp.value) {
    accounts.push({
      pubkey,
      accountInfo: SPL_ACCOUNT_LAYOUT.decode(account.data),
    })
  }

  return accounts
}

export async function swap(connection, poolKeys, publicKey, tokenAccounts, sendTransaction) {
  console.log('swap start')

  const poolInfo = await Liquidity.fetchInfo({ connection, poolKeys })

  // real amount = 1000000 / 10**poolInfo.baseDecimals
  const amountIn = new TokenAmount(new Token(poolKeys.baseMint, poolInfo.baseDecimals), 1, false)

  const currencyOut = new Token(poolKeys.quoteMint, poolInfo.quoteDecimals)

  // 5% slippage
  const slippage = new Percent(5, 100)

  const { amountOut, minAmountOut, currentPrice, executionPrice, priceImpact, fee } = Liquidity.computeAmountOut({
    poolKeys,
    poolInfo,
    amountIn,
    currencyOut,
    slippage,
  })

  // @ts-ignore
  // console.log(amountOut.toFixed(), minAmountOut.toFixed(), currentPrice.toFixed(), executionPrice.toFixed(), priceImpact.toFixed(), fee.toFixed())
  console.log(
    `swap: ${poolKeys.id.toBase58()}, amountIn: ${amountIn.toFixed()}, amountOut: ${amountOut.toFixed()}, executionPrice: ${executionPrice.toFixed()}`,
  )

  // const minAmountOut = new TokenAmount(new Token(poolKeys.quoteMint, poolInfo.quoteDecimals), 1000000)

  const { transaction, signers } = await Liquidity.makeSwapTransaction({
    connection,
    poolKeys,
    userKeys: {
      tokenAccounts,
      owner: publicKey,
    },
    amountIn,
    amountOut: minAmountOut,
    fixedSide: 'in',
  })

  await sendTx(connection, transaction, [...signers], sendTransaction)
  console.log('swap end')
}

