import { useMemo } from 'react'
import { useDispatch } from 'react-redux'
import { Web3ReactProvider } from '@web3-react/core'

import useEagerlyConnect from '../../hooks/useEagerlyConnect'
import useOrderedConnections from '../../hooks/useOrderedConnections'
import { getConnectionName } from '../../connection/utils'

export default function Web3Provider({ children }) {
  const dispatch = useDispatch()

  // Set metamask error handler for metamask disconnection warning modal.
  // useEffect(() => {
  //   setMetMaskErrorHandler((error) =>
  //     dispatch(updateConnectionError({ connectionType: ConnectionType.INJECTED, error: error.message })),
  //   )
  // }, [dispatch])

  useEagerlyConnect()
  const connections = useOrderedConnections()
  const connectors = connections.map(({ hooks, connector }) => [connector, hooks])

  const key = useMemo(() => connections.map(({ type }) => getConnectionName(type)).join('-'), [connections])

  return (
    <Web3ReactProvider connectors={connectors} key={key}>
      {children}
    </Web3ReactProvider>
  )
}
