import PHANTOM_ICON_URL from '../../components/Svgs/phantomWalletIcon.svg'
import { PhantomWalletName } from '@solana/wallet-adapter-wallets'
import Option from './Option'

const BASE_PROPS = {
  color: '#315CF5',
  icon: PHANTOM_ICON_URL,
  id: 'phantom-wallet',
}

export function InstallPhantomOption() {
  return <Option {...BASE_PROPS} header={'Install Phantom'} link="https://phantom.app/" />
}

export function OpenPhantomWalletOption() {
  const domain = window ? window.location.href : ''
  return (
    <Option
      {...BASE_PROPS}
      isActive={false}
      link={`https://phantom.app/ul/browse/${domain}`}
      header="Open in Phantom Wallet app"
    />
  )
}

export function PhantomWalletOption({ tryActivation }) {
  return <Option {...BASE_PROPS} isActive={false} onClick={() => tryActivation(PhantomWalletName)} header={'Phantom'} />
}
