import { Box, Modal, ModalBody, ModalCloseButton, ModalContent, ModalOverlay } from '@chakra-ui/react'
import React, { useCallback, useState } from 'react'
import { useDispatch } from 'react-redux'
import PendingView from '../../components/WalletModal/PendingView'
import WalletOptions from '../../components/WalletModal/WalletOptions'
import { getConnection } from '../../connection/utils'
import { updateSelectedWallet } from '../../state/user/reducer'
import { showConnect } from '@stacks/connect'
import useStacks from './hooks/useStacks'
import StacksWalletOptions from './StacksWalletOptions'

const WALLET_VIEWS = {
  OPTIONS: 'options',
  ACCOUNT: 'account',
  PENDING: 'pending',
}

export const StacksWalletModal = ({ isOpen, onClose }) => {
  return (
    <Modal isOpen={isOpen} onClose={onClose} isCentered size="xs" portalProps={'containerRef'}>
      <ModalOverlay />
      <ModalContent>
        <ModalCloseButton />
        <ModalBody>
          <StacksWalletModalView />
        </ModalBody>
      </ModalContent>
    </Modal>
  )
}

const StacksWalletModalView = ({ clientConfig }) => {
  const [walletView, setWalletView] = useState(WALLET_VIEWS.OPTIONS)

  const { userSession } = useStacks()

  const dispatch = useDispatch()

  const { profile } = clientConfig

  const tryActivation = useCallback(async (connector) => {
    try {
      const { name, profileImage } = profile
      setWalletView(WALLET_VIEWS.PENDING)
      showConnect({
        appDetails: {
          name: name,
          icon: profileImage,
        },
        redirectTo: '/',
        onFinish: () => {
          dispatch(updateSelectedWallet({ wallet: connector }))
        },
        userSession,
      })
    } catch (error) {
      setWalletView(WALLET_VIEWS.OPTIONS)
      console.debug(`connection error: ${error}`)
    }
  }, [])

  const getModalContent = () => {
    if (walletView === WALLET_VIEWS.OPTIONS) {
      return <StacksWalletOptions tryActivation={tryActivation} />
    }

    if (walletView === WALLET_VIEWS.PENDING) {
      return <PendingView goBack={() => setWalletView(WALLET_VIEWS.OPTIONS)} />
    }
  }
  return <Box>{getModalContent()}</Box>
}

export default StacksWalletModalView
